.event-card-main {
    border-radius: 5px;
    /* box-shadow: 0 2px 10px rgb(0 0 0 / 30%); */
    border: 1px solid;
    max-width: 600px;
    width: 100%;
    position: relative;
    padding: 20px 30px;
    background: #fff;
    margin: auto;
}
.event-card-main h5{margin: 0; width: 25%;}
.event-card-main .panel {
    display: flex;
    justify-content: flex-start;
    align-items: center;
}


